import request from '../../request'

// 
function retrievePassword(data) {
  return request({
    url: '/auth/api/retrievePassword',
    method: 'post',
    data: data
  })
}
// 
function getCompanyInfo(params) {
  return request({
    url: '/company/query/companyInfoByDomainName',
    method: 'get',
    params
  })
}
// 
function checkPassword(data) {
  return request({
    url: '/auth/api/checkPassword',
    method: 'post',
    data: data
  })
}

// 
function sendVeriCode(data) {
  return request({
    url: '/veriCode/api/send',
    method: 'post',
    data: data
  })
}
//
function getLoginInfo(data) {
  return request({
    url: '/auth/query/loginInfo',
    method: 'get',
    data
  })
}
//
function loginOut(data) {
  return request({
    url: '/auth/api/logout',
    method: 'post',
    data
  })
}

// 
function changePasswordByPwd(data) {
  return request({
    url: '/auth/api/changePassword',
    method: 'post',
    data
  });
}

export default{
  retrievePassword,
  getCompanyInfo,
  checkPassword,
  sendVeriCode,
  getLoginInfo,
  loginOut,
  changePasswordByPwd
}
