import request from '../../../request'

//  
function  changeInfo(data) {
  return request({
    url: '/dutyBasic/api/changeInfo',
    method: 'post',
    data
  })
}
//  
function  changeList(data) {
  return request({
    url: '/dutyBasic/api/changeList',
    method: 'post',
    data
  })
}

//  
function  getQueryPage(data) {
  return request({
    url: '/dutyBasic/query/getQueryPage',
    method: 'get',
    params: data
  })
}

//  
function  changeShowContent(data) {
  return request({
    url: '/dutyParameter/api/changeShowContent',
    method: 'post',
    data
  })
}

// 
function  getInfo(data) {
  return request({
    url: '/parameterCompanyShowType/query/getInfo',
    method: 'get',
    params: data
  })
}

export default {
  changeInfo,
  changeList,
  getQueryPage,
  changeShowContent,
  getInfo
}


