import request from '../../../request'

//  
function  getQueryPage(data) {
  return request({
    url: '/maintenanceWarnDayInfo/query/getQueryPage',
    method: 'get',
    params: data
  })
}

//  
function  changeInfo(data) {
  return request({
    url: '/maintenanceWarnDayInfo/api/changeInfo',
    method: 'post',
    data
  })
}
export default {
  getQueryPage,
  changeInfo
}



