import request from '../request'

/**/
function textLogin(data) {
  return request({
    url: '/auth/api/login',
    method: 'post',
    data
  })
}

/**/
function startInstance(data) {
  return request({
    url: '/workflow/api/startInstance',
    method: 'post',
    data
  })
}

/**/
function tasks(data) {
  return request({
    url: '/workflow/query/tasks',
    method: 'get',
    params: data
  })
}

/**/
function create(data) {
  return request({
    url: '/workflow/api/create',
    method: 'post',
    data
  })
}

/**/
function handle(data) {
  return request({
    url: '/workflow/api/handle',
    method: 'post',
    data
  })
}

/**/
function taskHistories(data) {
  return request({
    url: '/workflowHistory/query/taskHistories',
    method: 'get',
    params: data
  })
}


/**/
function applications(data) {
  return request({
    url: '/workflowHistory/query/applications',
    method: 'get',
    params: data
  })
}


/* */
function candidateStaffInfo(data) {
  return request({
    url: '/flowTaskCandidate/query/candidateStaffInfo',
    method: 'get',
    params: data
  })
}

/* */
function claim(data) {
  return request({
    url: '/workflow/api/claim',
    method: 'post',
    data
  })
}

/* */
function activeTask(data) {
  return request({
    url: '/workflow/query/activeTask',
    method: 'get',
    params: data
  })
}
export default {
  textLogin,
  startInstance,
  tasks,
  create,
  handle,
  taskHistories,
  applications,
  candidateStaffInfo,
  claim,
  activeTask
}


